<template>
    <div class="home" @mousemove="cursorchange()">
        <div class="cursor">
            <div></div>
        </div>
        <HEADER :logo="logo" @showMenu="show" color="white"></HEADER>
    
        <div class="yehjimwork">
            <span>YEHJIM WORK</span>
        </div>
        <div class="top">
            <div class="none">
                <span>NONE</span>
            </div>
            <div class="nocircle"></div>
        </div>
        <div class="logo">
            <Logo></Logo>
        </div>
        <div class="bottom">
            <div class="circle"></div>
            <div class="one">
                <span>ONE</span>
            </div>
        </div>
        <div class="made">
            <span>MADE BY MINE</span>
        </div>
        <div class="hidemenu">
            <Hidemenu @closeMenu="close"></Hidemenu>
        </div>
    
    
    </div>
</template>

<script>
// @ is an alias to /src
import Logo from '../components/Logo'
import HEADER from '../components/Header'
import Hidemenu from '../components/HideMenu'
import gsap from "gsap";

export default {
    name: 'Home',
    data() {
        return {
            logo: false,
        }
    },
    mounted() {
        var tl = gsap.timeline();
        tl.from('.none', {
            opacity: 0,
            duration: 1
        });
        tl.from('.nocircle', {
            opacity: 0,
            duration: 0.5
        });
        tl.from('.logo', {
            opacity: 0,
            duration: 1
        });
        tl.from('.circle', {
            opacity: 0,
            duration: 0.5

        });
        tl.from('.one', {
            opacity: 0,
            duration: 0.5

        });
    },
    methods: {
        show() {
            gsap.to('.hidemenu', {
                left: 0,
                opacity: 1,
                duration: 0.5
            })
            // this.left = '0%'
            // this.display = 'block'
            // console.log("有暗道")
        },
        close() {
            gsap.to('.hidemenu', {
                left: '100%',
                opacity: 0,
                duration: 0.5
            })
        },
        cursorchange() {
            // console.log(event)
            const cursor = document.querySelector('.cursor');
            cursor.setAttribute("style", "top: " + (event.pageY - 10) + "px; left: " + (event.pageX - 10) + "px;")

        }
    },
    components: {
        Logo,
        HEADER,
        Hidemenu
    }
}
</script>

