<template>
    <div class="code">
        <HEADER logo="true" color="black" @showMenu="show"></HEADER>
        <exppage></exppage>
        <div class="hidemenu">
            <hidemenu @closeMenu="close"></hidemenu>
        </div>
         <div v-if="isloading">
              <loadingpage></loadingpage>
         </div>
       
    </div>
</template>

<script>
import HEADER from '../components/Header'
import hidemenu from '../components/HideMenu'
import gsap from "gsap";
import loadingpage from '../components/Loadingpage'
import exppage from '../components/exp'
export default {
     data() {
        return {
            isloading: true
        }
    },
    mounted() {
        setTimeout(this.closeloading, 4000);
    },
    methods: {
        show() {
            gsap.to('.hidemenu', {
                left: 0,
                opacity: 1,
                duration: 0.5
            })
            // this.left = '0%'
            // this.display = 'block'
            // console.log("有暗道")
        },
        close() {
            gsap.to('.hidemenu', {
                left: '100%',
                opacity: 0,
                duration: 0.5
            })
        },
        closeloading() {
            this.isloading = false
        }
    },
    components: {
        HEADER,
        hidemenu,
        loadingpage,
        exppage
    }
}
</script>

<style lang="scss" scoped>
.code {
    height: 100vh;
    position: relative;
    overflow: hidden;
    // background-color: black;
}
.hidemenu {
    position: absolute; // top: -100%;
    top: 0;
    left: 100%; // left: 100%;
    // overflow: hidden;
}


</style>